<template>
    <div class="login-container" :style="{ backgroundImage: `url(${backgroundImage})` }">
        <el-card class="login-card">
            <h2 class="login-title">AI Art Generator</h2>
            <el-tabs v-model="activeTab" stretch>
                <!-- 手机号 + 密码登录 -->
                <el-tab-pane label="密码登录" name="password">
                    <el-form :model="passwordLoginForm" ref="passwordLoginForm">
                        <el-form-item prop="phone">
                            <el-input v-model="passwordLoginForm.phone" placeholder="请输入手机号"></el-input>
                        </el-form-item>
                        <el-form-item prop="password">
                            <el-input
                                type="password"
                                v-model="passwordLoginForm.password"
                                placeholder="请输入密码"
                                show-password
                            ></el-input>
                        </el-form-item>
                        <el-button type="primary" @click="submitPasswordForm" class="login-button">登录</el-button>
                    </el-form>
                </el-tab-pane>
                <!-- 短信验证码登录 -->
                <el-tab-pane label="短信验证登录" name="code">
                    <el-form :model="codeLoginForm" ref="codeLoginForm">
                        <el-form-item prop="phone">
                            <el-input v-model="codeLoginForm.phone" placeholder="请输入手机号"></el-input>
                        </el-form-item>
                        <el-form-item prop="code">
                            <el-input v-model="codeLoginForm.code" placeholder="请输入验证码">
                                <template #append>
                                    <el-button type="primary" :disabled="isCodeSending" @click="sendCode">
                                        {{ codeButtonText }}
                                    </el-button>
                                </template>
                            </el-input>
                        </el-form-item>
                        <el-button type="primary" @click="submitCodeForm" class="login-button">登录</el-button>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
            <p class="register-link">
                没有账号？<router-link to="/register">立即注册</router-link>
            </p>
        </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
            backgroundImage: 'https://img.freepik.com/free-photo/abstract-colorful-splash-3d-background-generative-ai-background_60438-2509.jpg',
            activeTab: 'password', // 默认显示密码登录
            passwordLoginForm: {
                phone: '',
                password: ''
            },
            codeLoginForm: {
                phone: '',
                code: ''
            },
            isCodeSending: false,
            codeButtonText: '获取验证码'
        };
    },
    methods: {
        // 检查手机号是否合法
        validatePhone(phone) {
            if (!phone) {
                this.$message.error('手机号不能为空');
                return false;
            }
            if (!/^1[3-9]\d{9}$/.test(phone)) {
                this.$message.error('请输入合法的手机号');
                return false;
            }
            return true;
        },
        // 提交密码登录表单
        submitPasswordForm() {
            const { phone, password } = this.passwordLoginForm;
            if (!phone || !password) {
                this.$message.error('手机号或密码不能为空');
                return;
            }
            // 直接提交表单，其他情况提示登录失败
            console.log('密码登录表单数据:', this.passwordLoginForm);
            
            // 模拟登录成功
            // 实际项目中应该调用API进行验证
            if (phone && password) {
                // 设置登录状态
                sessionStorage.setItem('isAuthenticated', 'true');
                sessionStorage.setItem('userPhone', phone);
                this.$message.success('登录成功');
                // 跳转到主页
                this.$router.push('/home');
            } else {
                this.$message.error('登录失败，请检查手机号或密码');
            }
        },
        // 提交验证码登录表单
        submitCodeForm() {
            const { phone, code } = this.codeLoginForm;
            if (!phone || !code) {
                this.$message.error('手机号或验证码不能为空');
                return;
            }
            if (!this.validatePhone(phone)) {
                return;
            }
            // 直接提交表单，其他情况提示登录失败
            console.log('验证码登录表单数据:', this.codeLoginForm);
            
            // 模拟登录成功
            // 实际项目中应该调用API进行验证
            if (phone && code) {
                // 设置登录状态
                sessionStorage.setItem('isAuthenticated', 'true');
                sessionStorage.setItem('userPhone', phone);
                this.$message.success('登录成功');
                // 跳转到主页
                this.$router.push('/home');
            } else {
                this.$message.error('登录失败，请检查手机号或验证码');
            }
        },
        // 发送验证码
        sendCode() {
            const phone = this.codeLoginForm.phone;
            if (!this.validatePhone(phone)) {
                return;
            }
            // 模拟发送验证码
            this.isCodeSending = true;
            this.codeButtonText = '发送中...';
            setTimeout(() => {
                this.isCodeSending = false;
                this.codeButtonText = '获取验证码';
                this.$message.success('验证码已发送');
            }, 3000);
        }
    }
};
</script>

<style scoped>
.login-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
}

.login-card {
    width: 400px;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.login-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
}

.login-button {
    width: 100%;
    margin-top: 20px;
}

.register-link {
    text-align: center;
    margin-top: 20px;
}

.register-link a {
    color: #409EFF;
    text-decoration: none;
}
</style>